<template>
  <div id="app">
    <div class="title">
      <h1>
        查旧物估价，用大数据
      </h1>
    </div>
    <a-row>
      <a-col :span="18">
        <div style="margin-left: 12px">
          <a-input v-model="name"></a-input>
        </div>
      </a-col>
      <a-col :span="1"></a-col>
      <a-col :span="5">
        <a-button @click="handleSubmit" type="primary">查询</a-button>
      </a-col>
    </a-row>
    <div v-if="haveSearched"  class="info" style="padding: 20px;font-size: 20px;">
      <p>共搜索到了 <span class="primary">{{result.count}}</span> 条数据</p>
      <p>其中，平均价格为</p>
      <div style="display: flex;align-items: center;text-align: center">￥：<span style="font-size: 40px;color: red">{{result.price}}</span></div>
    </div>
    <div v-if="!haveSearched" class="info">
      <div>快来点击查询吧</div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'App',
  components: {
  },
  data(){
    return {
      loading:false,
      name:'',
      result:{
        count:0,
        price:0,
      },
      haveSearched:false,
    }
  },
  methods:{
    handleSubmit(){
      if(this.name.trim() === ''){
        this.$message.error('请输入查询的名字')
        return
      }
      this.loading = true
      this.$message.success('正在查询')
      setTimeout(()=>{
        this.$message.error('抱歉，当前服务器繁忙')
        this.haveSearched = true
      },4000)
    },
  }

}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
.primary{
  font-size: 40px;
  color: red;
}
.title{
  padding: 14px;
  text-align: center;
  margin-top: 10px;
}
.info{
  font-size: 20px;
  padding: 20px;
}
</style>
